.floating-notification {
  position: fixed;
  z-index: 1000;
  background-color: #fff8e1;
  color: #333;
  border: 1px solid #ffb300;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
  animation: bounce-in 1s ease-out forwards; /* Animación de rebote al aparecer */
}

.floating-notification.fixed-top {
  position: fixed;
  top: 20%; /* 20% desde arriba */
  left: 50%; /* Centrado horizontalmente */
  transform: translateX(-50%);
  z-index: 1050; /* Asegúrate de que esté encima de otros elementos */
}

.floating-notification.bottom {
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.floating-notification.top {
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.floating-notification strong {
  font-weight: bold;
  color: #ff6f00;
}

.floating-notification:hover {
  background-color: #fff3b0;
  cursor: pointer;
}

.floating-notification:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(255, 179, 0, 0.6);
}

.floating-notification.hide {
  animation: bounce-out 0.5s forwards; /* Animación de rebote al desaparecer */
}
.floating-notification.hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none; /* Impide la interacción con la notificación cuando está oculta */
}

.cost-notification {
  border: 2px solid #000; /* Bordes negros */
  animation: bounce 1s ease infinite; /* Animación especial */
}

/* Animaciones */
@keyframes bounce-in {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  50% {
    transform: translateY(20px); /* Rebote hacia abajo */
  }
  70% {
    transform: translateY(-10px); /* Rebote hacia arriba */
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce-out {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px); /* Rebote hacia abajo al desaparecer */
  }
  70% {
    transform: translateY(-5px); /* Rebote hacia arriba al desaparecer */
  }
  100% {
    opacity: 0;
    transform: translateY(20px); /* Desaparece con un rebote final */
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}
