/* Botón de información */
.info-button {
    position: fixed;
    top: 4px;
    right: 15px;
    background-color: #007bff;
    color: white;
    border: none;
    padding: 12px 18px;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    font-weight: bold;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
  }
  
  .info-button:hover {
    background-color: #0056b3;
    transform: scale(1.05);
  }
  
  /* Fondo oscuro con desenfoque */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width:105vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;
    animation: fadeIn 0.3s ease-in-out;
  }
  
  /* Contenido del modal */
  .modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 15px;
    max-width: 90%;
    width: 400px;
    max-height: 80vh;
    overflow-y: auto;
    text-align: center;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
    animation: slideIn 0.3s ease-in-out;
  }
  
  /* Títulos */
  .modal-content h2 {
    font-size: 22px;
    margin-bottom: 15px;
    text-align: center;
    color: #000000;
    border-bottom: 2px solid #0050a7;
    display: inline-block; /* 🔹 Esto permite que el subrayado no ocupe toda la pantalla */
    padding-bottom: 5px;
  }
  
  .modal-content h3 {
    font-size: 18px;
    margin-top: 15px;
    color: #007bff;
    font-weight: bold;
  }
  
  /* Listas */
  .modal-content ul {
    padding-left: 20px;
    margin-bottom: 10px;
    border-bottom: 2px solid #0050a7;

  }
  
  .modal-content ul li {
    font-size: 14px;
    color: #555;
    margin-bottom: 5px;
  }
  
  /* Párrafos */
  .modal-content p {
    font-size: 14px;
    color: #444;
    line-height: 1.5;
    border-bottom: 2px solid #0050a7;

  }
  
  /* Botón de cierre */
  .close-button {
    display: block;
    width: 100%;
    margin-top: 20px;
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 12px;
    cursor: pointer;
    border-radius: 10px;
    font-size: 16px;
    font-weight: bold;
    transition: all 0.3s ease;
  }
  
  .close-button:hover {
    background-color: #cc0000;
    transform: scale(1.05);
  }
  
  /* Scroll mejorado */
  .modal-content::-webkit-scrollbar {
    width: 6px;
  }
  
  .modal-content::-webkit-scrollbar-thumb {
    background-color: #007bff;
    border-radius: 5px;
  }
  
  .modal-content::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }
  
  /* Animaciones */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes slideIn {
    from {
      transform: translateY(-20px);
    }
    to {
      transform: translateY(0);
    }
  }
  