.match {
  grid-template-columns: repeat(3, minmax(80px, 1fr)); /* Ajustar tamaño de las columnas */
  gap: 8px;
  grid-row-gap: 2px;
  align-items: center;
  padding: 6px;
  font-size: 14px;
  border: 1px solid #000000;
  border-radius: 8px;
  background: linear-gradient(45deg, #000428d7, #004e92d0);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease-in-out;
  max-width: 100%;
  width: 100%;
  margin: 0 auto;
}

.team, .result {
  min-width: 10px;
  min-height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3px;
  border-radius: 15px;
  flex-direction:column-reverse;
  background: linear-gradient(45deg, #0004287d, #004e926c);
  transition: all 0.3s ease-in-out;
  border: 0.5px solid #000000;
}

.match:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}



.match-informacion {
  grid-column: 1 / -1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9em;
  color: #ffffffc4;
  margin-bottom: 12px;
  white-space: nowrap;
  padding-bottom: 5px;
  border-bottom: 1px solid #000000aa;
  gap: 5px;
}

.match-info {
  width: 110%;
  margin-bottom: 1px;
  display: flex;
  justify-content: space-between;
  font-size: 0.9em;
  color: #ffffffc4;
}

.match-info span {
  display: flex;
  align-items: center;
  gap: 5px;
}
.match-info .match-time,
.match-info .match-stadium {
  width: 100%;
}

.match-info {
  display: flex;
  align-items: center;
  gap: 8px; /* Espacio entre el icono y el texto */
}

.match-info i {
  color: #ffffff;
  font-size: 1.2em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 1; /* Asegura que el ícono no tenga espacios extra */
  width: 16px; /* Ajusta el tamaño del ícono */
  height: 16px;
}


.match-date {
  display: flex;
  align-items: center;
  gap: 5px;
}

.team-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.team img {
  max-width: 40px; /* Reducir el tamaño máximo de la imagen */
  max-height: 40px;
}

.team-name, .result {
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2); /* Menos fuerte */
  text-align: center;
}

.result {
  font-weight: bold;
  color: #000000;
  border-radius: 15px;
  background-color: #ffffff;
  border: 1px solid #ddd;
}

.team:hover, .result:hover {
  background-color: #e0e0e0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.team.selected, .result.selected {
  background-color: #0fe716f6;
  color: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease-in-out;
}

.team.selected:hover, .result.selected:hover {
  background-color: #00ff08;
}

/* Efecto de animación bounce */
@keyframes bounce {
  0% { transform: scale(1); }
  25% { transform: scale(1.1); }
  50% { transform: scale(1); }
}

/* Estilo para el botón destacado */
.highlight-button {
  background-color: #ff9800;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.highlight-button:hover {
  background-color: #ffb300; /* Un naranja más suave */
}


@media (max-width: 600px) {
  .match {
    grid-template-columns: 1fr;
    padding: 8px;
  }

  .team-content, .result {
    flex-direction:column-reverse;
    gap: 1px;
    margin-bottom: auto;

  }

  .team img {
    max-width: 35px;
    max-height: 35px;
  }

  .match-info {
    font-size: 0.8em;
  }
}
@media (min-width: 1024px) {
  .match {
    display: auto;
    grid-template-columns: repeat(3, minmax(100px, 1fr)); /* Más columnas en pantallas grandes */
    font-size: auto; /* Aumentar el tamaño de la fuente */
    padding: auto;
    max-width: auto; /* Limitar el ancho para que no se vea demasiado extendido */
    margin: auto; /* Centrar el contenido */
    gap: auto; /* Espaciado uniforme */
  }

  .match-info {
    font-size: auto;
    display: auto;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }

  .team {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    background: linear-gradient(45deg, #0004287d, #004e926c);
    border: 0.5px solid #000000;
    border-radius: 15px;
    min-width: 80px;
    min-height: 60px;
    transition: all 0.3s ease-in-out;
  }

  .team img {
    max-width: 50px;
    max-height: 50px;
  }

  .team-content {
    display:contents;
    align-items: center;
    gap: -5px;
  }

  .team-name {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    color: white;
    text-align: center;
  }

  .result {
    font-size: 18px;
    min-width: 80px;
    padding: 8px;
    text-align: center;
    font-weight: bold;
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 15px;
    transition: all 0.3s ease-in-out;
  }

  .team:hover, .result:hover {
    background-color: #e0e0e0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }

  .team.selected, .result.selected {
    background-color: #0fe716f6;
    color: white;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.5s ease-in-out;
  }

  .team.selected:hover, .result.selected:hover {
    background-color: #00ff08;
  }
}


